.contact {
  margin: 78px 0 140px;
  h3 {
    margin-bottom: 52px;
    font-size: 22px;
    color: #1f1f1f;
  }
  .notice {
    color: #898989;
    line-height: 1.8571;
    margin-bottom: 60px;
    padding: 34px;
    background-color: #f8f8f8;
    li {
      &::before {
        content: '';
        vertical-align: middle;
        display: inline-block;
        width: 2px;
        height: 2px;
        margin-right: 6px;
        background-color: #aaa;
      }
      a {
        color: #1f1f1f;
      }
    }
  }
  .consulting-history {
    margin-bottom: 50px;
    padding: 60px 0 40px;
    border-top: 4px solid #1f1f1f;
    border-bottom: 1px solid #1f1f1f;
    .history,
    .consulting {
      vertical-align: middle;
      display: inline-block;
      box-sizing: border-box;
    }
    .history {
      text-align: center;
      width: 528px;
      li {
        position: relative;
        display: inline-block;
        width: 150px;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 8px;
          display: block;
          width: 1px;
          height: 60px;
          background-color: #f1f1f1;
        }
        &:first-child:before {
          display: none;
        }
        strong,
        span {
          display: block;
        }
        strong {
          margin-bottom: 15px;
          font-size: 14px;
          font-weight: normal;
        }
        span {
          font-size: 40px;
          font-weight: bold;
        }
      }
    }
    .consulting {
      font-size: 14px;
      .notice {
        margin-bottom: 20px;
        padding: 0;
        background-color: transparent;
      }
      .btn {
        color: #000;
        border: 1px solid #dedede;
        background-color: #fff;
      }
    }
  }
  .question-form {
    font-size: 14px;
    dt,
    dd {
      float: left;
      margin-bottom: 40px;
    }
    dt {
      width: 100px;
      .required {
        font: 0/0 a;
        vertical-align: middle;
        display: inline-block;
        width: 4px;
        height: 4px;
        margin-left: 5px;
        border-radius: 50%;
        background-color: #f23d4e;
      }
    }
    dd {
      width: 872px;
      min-height: 25px;
      .select {
        position: relative;
        top: -13px;
      }
      .row {
        &::after {
          content: '';
          display: block;
          clear: both;
        }
      }
      .col-1 {
        float: left;
        width: 374px;
        margin-right: 24px;
      }
      .col-2 {
        float: left;
        width: 244px;
      }
      .inline-form {
        display: table;
        input,
        .split {
          display: table-cell;
        }
        .split {
          vertical-align: middle;
          padding: 0 5px;
        }
      }
      .buttons {
        margin-top: -9px;
      }
      .add-file {
        float: left;
        .file-upload-wrapper {
          display: inline-block;
          position: relative;
          .upload-file-wrapper {
            span {
              display: inline-block;
              max-width: 530px;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              line-height: 1.1;
            }
          }
        }
        .file {
          vertical-align: top;
          position: relative;
          overflow: hidden;
          display: inline-block;
          margin-top: -9px;
          input[type="file"] {
            font-size: 100px;
            vertical-align: top;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
          }
          .upload-file {
            max-width: 350px;
            display: inline-block;
            margin-top: 15px;
            overflow:hidden;
            white-space:nowrap;
            text-overflow:ellipsis;
          }
        }
        .thumb {
          float: left;
          text-align: center;
          white-space: nowrap;
          position: relative;
          display: inline-block;
          width: 78px;
          height: 78px;
          margin: -9px 16px 0 0;
          border: 1px solid  #dedede;
          background-color: #000;
          .delete {
            font: 0/0 a;
            position: absolute;
            top: 0;
            right: 0;
            display: block;
            width: 20px;
            height: 20px;
            background: #000 url('/_ui/img/pc/icn/icn-file-delete.png') no-repeat 50% 50%;
          }
          canvas {
            height: 78px;
          }
          img {
            vertical-align: middle;
            display: inline-block;
            width: auto;
            max-width: 100%;
            max-height: 100%;
          }
          &::after {
            content: '';
            vertical-align: middle;
            display: inline-block;
            height: 100%;
          }
        }
        .thumb.hidden {
          display: none;
        }
      }
      .desc {
        color: #000;
        font-size: 12px;
        &::before {
          content: '';
          vertical-align: middle;
          display: inline-block;
          width: 2px;
          height: 2px;
          margin-right: 4px;
          background: #8a8a8a;
        }
      }
      .desc.inline {
        color: #898989;
        font-size: 14px;
        display: inline-block;
        margin-left: 15px;
        &::before {
          display: none;
        }
      }
      .select-product {
        position: relative;
        margin-top: 14px;
        padding: 22px 33px 18px 24px;
        border: 1px solid #dedede;
        .thumb {
          float: left;
          position: relative;
          margin-right: 20px;
          &::before {
            content: '';
            position: absolute;
            display: block;
            opacity: .03;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: #000;
            z-index: 1;
          }
          img {
            width: auto;
          }
        }
        .product {
          font-size: 14px;
          float: left;
          h4 {
            font-weight: bold;
            margin: 10px 0 13px;
          }
          p {
            height: 50px;
          }
        }
        .change-product {
          position: absolute;
          right: 33px;
          bottom: 18px;
        }
        .btn-close {
          font: 0/0 a;
          position: absolute;
          right: 33px;
          top: 22px;
          width: 15px;
          height: 15px;
          background: url('/_ui/img/pc/icn/icn-close.png') no-repeat;
        }
        &::after {
          content: '';
          display: block;
          clear: both;
        }
      }
      .content {
        color: #1f1f1f;
        line-height: 1.7143;
      }
      input[type="text"],
      textarea {
        width: 100%;
        margin-top: -13px;
        font-size: 14px;
      }
      input[type="text"] {
        height: 38px;
      }
      textarea {
        height: 130px;
        padding: 13px 0 0;
        resize: none;
        outline: none;
      }
      &::after {
        content: '';
        display: block;
        clear: both;
      }
    }
    &::after {
      content: '';
      display: block;
      clear: both;
    }
  }

  .qna-form {
    dl {
      dt.ques, .answer-wrap  {
        padding: 23px 45px 23px 34px;
        font-size: 0;
        span {
          display: inline-block;
          font-size: 14px;
          vertical-align: middle;
        }
        .process {
          width: 100px;
          vertical-align: top;
        }
        .subject {
          width: 72%;
        }
        .has-photo {
          width: 16px;
          height: 16px;
          vertical-align: sub;
          background: url(/_ui/img/pc/icn/icn-photo.png) no-repeat center;
          float: right;
          margin: 2px 29px 0px 0;
        }
        .date {
          width: 80px;
          float: right;
          text-align: center;
          font-size: 13.5px;
        }
        &::after {
          margin-top: -5px;
        }
      }
      dd.question-wrap {
        padding: 0 220px 34px 164px;
        background: none;
        color: #1f1f1f;
        .select-product {
          position: relative;
          margin: 14px 0;
          padding: 18px;
          border: 1px solid #dedede;
          .thumb {
            float: left;
            position: relative;
            margin-right: 20px;
            &::before {
              content: '';
              position: absolute;
              display: block;
              opacity: .03;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              background: #000;
              z-index: 1;
            }
            img {
              width: auto;
              max-width: 96px;
              height: 96px;
              object-fit: contain;
            }
          }
          .product {
            width: 466px;
            overflow: hidden;
            font-size: 14px;
            float: left;
            .brand {
              font-weight: 900;
              font-size: 13px;
            }
            p {
              margin: 5px 0 10px;
            }
            .code {
              float: right;
              color: #898989;
              font-size: 13px;
            }
            .price {
              .discount {
                color: #9ba4b0;
                margin-right: 5px;
              }
            }
          }
          &::after {
            content: '';
            display: block;
            clear: both;
          }
        }
      }
      dd.answer-wrap {
        padding: 23px 75px 23px 64px;
        background-position: 30px 24px;
        .img-link {
          margin-left: 100px;
        }
      }
      .img-link {
        margin-top: 20px;
        font-size: 14px;
        a {
          text-decoration: underline;
        }
      }
    }
  }
}

.layer-select-layer {
  .layer {
    .inner {
      padding-bottom: 0;
    }
    .content {
      padding-bottom: 60px;
      h2 {
        font-size: 14px;
      }
      .tab {
        padding-bottom: 0;
        a {
          padding: 10px 0;
        }
      }
      .search-date {
        margin: 0 0 40px;
        padding: 34px 30px;
        background-color: #f8f8f8;
        .tab {
          li {
            width: 73px;
            background-color: #fff;
            a {
              box-sizing: border-box;
              height: 30px;
              padding: 0;
              line-height: 30px;
              font-size: 12px;
            }
          }
          .on {
            background-color: #898989;
          }
        }
        .date {
          input,
          button,
          .split {
            line-height: 30px;
            height: 30px;
          }
          input {
            width: 116px;
            background-color: #fff;
            background-position: 78px 50%;
            font-size: 12px;
          }
          button {
            line-height: 31px;
            font-size: 12px;
          }
        }
      }
      .list {
        position: relative;
        display: table;
        width: 100%;
        padding: 16px 0;
        border-bottom: 1px solid #e8e8e8;
        .summary {
          font-size: 12px;
          position: absolute;
          top: 30px;
          left: 0;
          .date {
            color: #898989;
            margin-left: 10px;
          }
        }
        .thumb {
          display: table-cell;
          width: 72px;
          a {
            img {
              width: 100%;
            }
          }
        }
        .product,
        .options,
        .price,
        .select {
          vertical-align: middle;
          display: table-cell;
        }
        .product {
          padding-left: 20px;
          padding-right: 20px;
          h3 {
            font-size: 10px;
            font-weight: 700;
          }
          p {
            font-size: 15px;
            height: 22px;
            margin: 15px 0;
            a {
              color: #1f1f1f;
              max-width: 300px;
              display: block;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
            }
          }
          small {
            color: #898989;
            font-size: 11px;
          }
        }
        .options {
          color: #1f1f1f;
          font-size: 12px;
          width: 140px;
          strong {
            font-weight: normal;
            display: inline-block;
            width: 32px;
          }
        }
        .price {
          width: 120px;
          padding-right: 20px;
          font-size: 15px;
        }
        .select {
          width: 86px;
          .btn {
            width: 100%;
            padding: 0;
          }
        }
      }
      .lists {
        .no-result {
          color: #898989;
          font-size: 16px;
          text-align: center;
          padding: 130px 0;
        }
      }
      .tab-cnt-1 {
        .list {
          padding-top: 55px;
        }
      }
    }
  }
}